/*==========  Desktop First Method  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1599px) {
    #header .container{
        padding: 21px 70px 0 70px;
    }
    .title_sec{
        margin: 0;
    }
    .block_box{
        padding: 100px 10px 100px 10px;
    }
    .box_inspired,
    .box_rewarded,
    .box_social{
        margin: 0 0 21px 97px;
    }
    .block_box_sec h1:after{
        left: 105px;
    }


    .block_cont_box{
        padding: 40px 0 0 0;
        position: relative;
    }
    .block_cont_box:before{
        top: 80px;
    }
    .block_cont_box p{
        margin: 0 0 15px 0;
        font: 16px/24px "Lato Light", Helvetica, Arial,sans-serif;
    }
    .inscription:after{
        top: 35px;
        left: 445px;
    }
    .title{
        margin: 0 auto -39px auto;
        top: 50px;
    }
    .title h1{
        font:italic bold 12px/12.81px Times New Roman, Arial,sans-serif,Helvetica;
    }
    .title h2{
        font: 24px/12.81px "Lato Bold", Arial,sans-serif,Helvetica;
    }
    .num_list ul{
        padding: 0 100px;
    }
    .num_list ul li h1{
        font: 30px/30px "Lato Bold",Arial,Helvetica,sans-serif;
    }
    .num_list ul li p{
        margin: 0;
        font: 14px/18px "Lato Light", Arial,Helvetica,sans-serif;
    }
    #footer .container{
        padding: 41px 100px;
    }
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
    .box_inspired,
    .box_rewarded,
    .box_social{
        margin: 0 0 21px 55px;
    }
    .block_container{
        padding: 102px 10px 0 10px;
    }
    .block_cont_box{
        padding: 0;
    }
    .block_cont_box:before{
        top: 30px;
    }
    .block_cont_box h1{
        margin: 0 0 22px 0;
        font: 16px/20px "Lato Bold", Helvetica, Arial,sans-serif;
    }
    .block_cont_box p{
        margin: 0 0 15px 0;
        font: 14px/22px "Lato Light", Helvetica, Arial,sans-serif;
    }
    .app_store img{
        max-width:50%;
        height: auto;
        display: inline-block;
    }
    .google_play img{
        max-width:50%;
        height: auto;
        display: inline-block;
    }
    .sld_box q{
        font: 26px/32px 'Lato Hairline', Arial, Helvetica, sans-serif;
        color: #fff;
    }
    .sld_box cite{
        font: 18px/36px 'Lato Hairline Italic', Arial, Helvetica, sans-serif;
    }
    .inscription:after{
        top: 35px;
        left: 335px;
    }
    .title{
        margin: 0 auto -46px auto;
        top: 35px;
    }
    .title h1{
        font:italic bold 9px/12.81px Times New Roman, Arial,sans-serif,Helvetica;
        margin: 0 0 10px 0;
    }
    .title h2{
        font: 18px/12.81px "Lato Bold", Arial,sans-serif,Helvetica;
        margin: 0 0 10px 0;
    }
    .num_list ul{
        padding: 0 70px;
    }
    .num_list ul li h1{
        font: 26px/30px "Lato Bold",Arial,Helvetica,sans-serif;
    }
    .num_list ul li p{
        margin: 0;
        font: 14px/18px "Lato Light", Arial,Helvetica,sans-serif;
    }
    #footer .container{
        padding: 41px 100px;
    }
    .copyright{
        font: 12px/16px "Lato Light", Arial,Helvetica,sans-serif;
    }
    .link{
        font: 12px/16px "Lato Light", Arial,Helvetica,sans-serif;
    }
    .link a{
        font: 12px/16px "Lato Regular", Arial,Helvetica,sans-serif;
    }
}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
    #nav ul li a{
        font: 12px/15px Lato Bold, Arial, Helvetica, sans-serif;
    }
    .title_sec h1{
        font:italic bold 26px/29px Times New Roman, Arial, Helvetica, sans-serif;
    }
    .title_sec h2{
        font:bold 70px/74px Lato Regular, Arial, Helvetica, sans-serif;
    }
    .get_start{
        padding: 14px 49px 16px;
        font: bold 15px/18px Lato Regular, Arial, Helvetica, sans-serif;
    }
    .title_sec p {
        margin: 0;
        text-transform: uppercase;
        font: 11px/15px Lato Regular, Arial, Helvetica, sans-serif;
    }
    .title_sec p a{
        color: #fff;
        font: bold 11px/15px Lato Bold, Arial, Helvetica, sans-serif;
    }
    .block_box_sec{
        margin-bottom: 50px;
    }
    .box_inspired,
    .box_rewarded,
    .box_social{
        margin: 0 auto 21px auto;
    }
    .nav_row{
        margin: 0 0 140px 0;
    }
    .title_row{
        margin: 0 0 140px 0;
    }
    .block_container {
        padding-bottom: 60px;
    }
    .block_cont_box h1{
        font: 14px/18px "Lato Bold", Helvetica,  Arial, sans-serif;
    }
    .app_store img{
        max-width:45%;
        height: auto;
        display: inline-block;
    }
    .google_play img{
        max-width:45%;
        height: auto;
        display: inline-block;
    }
    .sld_box q{
        font: 22px/28px 'Lato Hairline', Arial, Helvetica, sans-serif;
        color: #fff;
    }
    .sld_box cite{
        font: 14px/28px 'Lato Hairline Italic', Arial, Helvetica, sans-serif;
    }
    .img_list{
        text-align: center;
    }
    .img_list li{
        width: 40%;
    }
    .inscription:after{
        top: 35px;
        left: 40%;
    }
    .title{
        margin: 0 auto -46px auto;
        top: 60px;
    }
    .title h1{
        font:italic bold 12px/12.81px Times New Roman, Arial,sans-serif,Helvetica;
    }
    .title h2{
        font: 24px/12.81px "Lato Bold", Arial,sans-serif,Helvetica;
    }
    .num_list ul{
        padding: 0 50px;
    }
    .num_list ul li{
        display: inline-block;
        width: 45%;
        margin: 0 0 20px 0;
    }
    .num_list ul li h1{
        font: 22px/28px "Lato Bold",Arial,Helvetica,sans-serif;
        margin: 0 0 10px 0;
    }
    .num_list ul li p{
        margin: 0;
        font: 12px/16px "Lato Light", Arial,Helvetica,sans-serif;
    }
    #footer .container{
        padding: 41px 100px;
    }
    .copyright{
        font: 12px/16px "Lato Light", Arial,Helvetica,sans-serif;
    }
    .link{
        font: 12px/16px "Lato Light", Arial,Helvetica,sans-serif;
    }
    .link a{
        font: 12px/16px "Lato Regular", Arial,Helvetica,sans-serif;
    }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
    #header .container{
        padding: 21px 60px 0 60px;
    }
    .nav_row .col-lg-12{
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        -webkit-flex-direction: column;
        -moz-flex-direction: column;
        -ms-flex-direction: column;
        -o-flex-direction: column;
        flex-direction: column;
    }
    .nav_row{
        margin: 0 0 50px 0;
    }
    .title_row{
        margin: 0 0 142px 0;
    }
    #nav{
        display: block;
        margin: 0 auto 50px auto;
        -webkit-order: 1;
        -moz-order: 1;
        -ms-order: 1;
        -o-order: 1;
        order: 1;
    }
    #nav ul li{
        margin: 0 0 0 35px;
    }
    #nav ul li a{
        font: 11px/14px Lato Bold, Arial, Helvetica, sans-serif;
    }
    #logo{
        display: block;
        margin: 0 auto;
        -webkit-order: 2;
        -moz-order: 2;
        -ms-order: 2;
        -o-order: 2;
        order: 2;
    }
    .title_sec h1{
        font:italic bold 17px/21px Times New Roman, Arial, Helvetica, sans-serif;
    }
    .title_sec h2{
        font:bold 50px/54px Lato Regular, Arial, Helvetica, sans-serif;
    }
    .get_start{
        padding: 10px 40px 12px;
        font: bold 11px/17px Lato Regular, Arial, Helvetica, sans-serif;
    }
    .title_sec p {
        margin: 0;
        text-transform: uppercase;
        font: 9px/13px Lato Regular, Arial, Helvetica, sans-serif;
    }
    .title_sec p a{
        color: #fff;
        font: bold 9px/13px Lato Bold, Arial, Helvetica, sans-serif;
    }
    .box_inspired,
    .box_rewarded,
    .box_social{
        width: 60px;
        height: 60px;
        margin: 0 auto 21px auto;
    }
    .box_inspired:after,
    .box_rewarded:after,
    .box_social:after{
        width: 58px;
        left: 0px;
        top: 100px;
    }
    .box_rewarded img{
        top: 19px;
        left: 9px;
        max-width: 70%;
    }
    .box_inspired img,
    .box_social img{
        top: 5px;
        left: 9px;
        max-width: 70%;
    }
    .block_box{
        padding: 60px 10px 30px 10px;
    }
    .block_box_sec h1{
        font: 14px/16px Lato Bold, Arial, Helvetica, sans-serif;
    }
    .block_box_sec p{
        font: 12px/14px Lato Light, Helvetica,  Arial,sans-serif;
    }
    .block_container{
        padding: 70px 10px 50px 10px;
    }
    .block_img{
        margin: 0 0 30px 0;
    }
    .block_cont_box h1{
        font: 12px/16px "Lato Bold", Helvetica,  Arial, sans-serif;
    }
    .block_cont_box p{
        font: 12px/18px "Lato Light", Helvetica, Arial,sans-serif;
    }
    .sld_box q{
        font: 16px/24px 'Lato Hairline', Arial, Helvetica, sans-serif;
        color: #fff;
    }
    .sld_box cite{
        font: 12px/26px 'Lato Hairline Italic', Arial, Helvetica, sans-serif;
    }
    .img_list li{
        width: 50%;
    }
    .inscription:after{
        top: 35px;
        left: 40%;
    }
    .title{
        margin: 0 auto -46px auto;
        top: 50px;
    }
    .title h1{
        font:italic bold 11px/12.81px Times New Roman, Arial,sans-serif,Helvetica;
    }
    .title h2{
        font: 20px/12.81px "Lato Bold", Arial,sans-serif,Helvetica;
    }
    .num_list ul li h1{
        font: 19px/26px "Lato Bold",Arial,Helvetica,sans-serif;
        margin: 0 0 10px 0;
    }
    .num_list ul li p{
        margin: 0;
        font: 12px/16px "Lato Light", Arial,Helvetica,sans-serif;
    }
    #footer .container{
        padding: 41px 40px;
        text-align: center;
    }
    .copyright{
        font: 12px/16px "Lato Light", Arial,Helvetica,sans-serif;
        display: block;
        float: none;
        margin: 0 0 20px 0;
    }
    .link{
        font: 12px/16px "Lato Light", Arial,Helvetica,sans-serif;
        display: block;
        float: none;
    }
    .link a{
        font: 12px/16px "Lato Regular", Arial,Helvetica,sans-serif;
    }
}

/* Custom, iPhone Retina */
@media only screen and (max-width : 320px) {
    #header .container{
        padding: 21px 30px 0 30px;
    }
    #nav ul li{
        margin: 0 0 0 15px;
    }
    .title_sec h1{
        font:italic bold 15px/20px Times New Roman, Arial, Helvetica, sans-serif;
    }
    .title_sec h2{
        font:bold 38px/42px Lato Regular, Arial, Helvetica, sans-serif;
    }
    .get_start{
        padding: 10px 25px 12px;
        font: bold 11px/17px Lato Regular, Arial, Helvetica, sans-serif;
    }
    .title_sec p {
        margin: 0;
        text-transform: uppercase;
        font: 9px/13px Lato Regular, Arial, Helvetica, sans-serif;
    }
    .title_sec p a{
        color: #fff;
        font: bold 9px/13px Lato Bold, Arial, Helvetica, sans-serif;
    }
    .img_list li{
        width: 100%;
    }
    .inscription:after{
        top: 35px;
        left: 35%;
    }
    .title{
        margin: 0 auto -46px auto;
        top: 80px;
    }
    .title h1{
        font:italic bold 15px/12.81px Times New Roman, Arial,sans-serif,Helvetica;
    }
    .title h2{
        font: 32px/12.81px "Lato Bold", Arial,sans-serif,Helvetica;
    }
}


